export default {
	data() {
		return {
			globalUser: {}
		};
	},
	onShow() {
		var me = this;
		var globalUser = me.getGlobalUser('ele_login');
		me.globalUser = globalUser;
	},
	methods: {
		operator() {
			var me = this;
			var globalUser = me.getGlobalUser('ele_login');
			uni.showActionSheet({
				itemList: ['查看我的头像', '从相册选择上传'],
				success(res) {
					var index = res.tapIndex;
					if (index == 0) {
						// 预览头像
						var faceArr = [];
						faceArr.push(globalUser.face);
						uni.previewImage({
							urls: faceArr,
							current: faceArr[0]
						});
					} else if (index == 1) {
						// 选择上传头像
						uni.chooseImage({
							count: 1,
							sizeType: ['compressed'],
							sourceType: ['album'],
							success(res) {
								// 获得临时路径
								var tempFilePath = res.tempFilePaths[0];
								






								uni.navigateTo({
									url: "../faceCrop/faceCrop?tempFilePath=" + tempFilePath
								})

							}
						});
					}
				}
			});
		},
		cleanStorage() {
			uni.clearStorageSync();
			uni.showToast({
				title: '清理缓存成功',
				mask: false,
				duration: 1500
			});
		},
		logout() {
			var globalUser = this.getGlobalUser('ele_login');
			var serverUrl = this.serverUrl;

			uni.removeStorageSync('ele_login');
			uni.switchTab({
				url: '../tabBar/user/user'
			});
			//没有退出登陆接口
			// this.request({
			// 	url: interfaces.logout,
			// 	data: {
			// 		userId: globalUser.userId,
			// 	},
			// 	method: 'POST',
			// 	success: res => {
			// 		uni.removeStorageSync("ele_login");
			// 		uni.switchTab({
			// 			url: "../tabBar/user/user"
			// 		})
			// 	}
			// });
		}
	}
};
